import React, { useState } from 'react';
import './Jobs.css';

const Jobs = () => {
  // Dummy data for jobs
  const jobsData = [
    {
      id: 1,
      title: 'Frontend Developer',
      company: 'Tech Solutions',
      location: 'New York, NY',
      type: 'Full-Time',
      category: 'Development',
      description: 'Looking for a skilled frontend developer with experience in React.',
    },
    {
      id: 2,
      title: 'Backend Developer',
      company: 'Web Innovators',
      location: 'San Francisco, CA',
      type: 'Part-Time',
      category: 'Development',
      description: 'Seeking a backend developer with experience in Node.js and Express.',
    },
    {
      id: 3,
      title: 'UI/UX Designer',
      company: 'Creative Minds',
      location: 'Remote',
      type: 'Contract',
      category: 'Design',
      description: 'We need a creative UI/UX designer to join our team.',
    },
    // Add more jobs here...
  ];

  // State for search, filter, and job data
  const [searchTerm, setSearchTerm] = useState('');
  const [jobType, setJobType] = useState('');
  const [location, setLocation] = useState('');
  const [filteredJobs, setFilteredJobs] = useState(jobsData);

  // Function to handle search and filtering
  const handleFilter = () => {
    const filtered = jobsData.filter((job) => {
      const matchesSearchTerm = job.title.toLowerCase().includes(searchTerm.toLowerCase()) || job.company.toLowerCase().includes(searchTerm.toLowerCase());
      const matchesType = jobType === '' || job.type === jobType;
      const matchesLocation = location === '' || job.location.toLowerCase().includes(location.toLowerCase());
      return matchesSearchTerm && matchesType && matchesLocation;
    });
    setFilteredJobs(filtered);
  };

  // UseEffect to handle filter when searchTerm, jobType, or location changes
  React.useEffect(() => {
    handleFilter();
  }, [searchTerm, jobType, location]);

  return (
    <div className="job-listing-page">
      <h2>Job Listings</h2>
      <div className="filters">
        <input
          type="text"
          placeholder="Search by title or company"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
        />
        <select value={jobType} onChange={(e) => setJobType(e.target.value)}>
          <option value="">All Types</option>
          <option value="Full-Time">Full-Time</option>
          <option value="Part-Time">Part-Time</option>
          <option value="Contract">Contract</option>
        </select>
        <input
          type="text"
          placeholder="Filter by location"
          value={location}
          onChange={(e) => setLocation(e.target.value)}
        />
      </div>

      <div className="job-listings">
        {filteredJobs.map((job) => (
          <div key={job.id} className="job-card">
            <h3>{job.title}</h3>
            <p><strong>Company:</strong> {job.company}</p>
            <p><strong>Location:</strong> {job.location}</p>
            <p><strong>Type:</strong> {job.type}</p>
            <p className="description">{job.description}</p>
            <button className="apply-button">Apply Now</button>
          </div>
        ))}
        {filteredJobs.length === 0 && <p>No jobs found matching your criteria.</p>}
      </div>
    </div>
  );
};

export default Jobs;
