import React, { useContext, useState } from 'react';
import { Container, TextField, Button, Typography, Box, Alert } from '@mui/material';
import './Register.css';
import { useNavigate } from 'react-router-dom';
const Login = ({setCurrentUser,userList}) => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const navigate=useNavigate();
  const loginSample = async (e) => {
    
    let fetchUser=()=>{
    // document.querySelector(".container .formElement input[type=text]").value="";
    try{
      for(let i=0; i<userList.length; i++){
        if(userList[i].email===email){
          setCurrentUser(userList[i]);
          return;
        }
      }
      throw new Error("Invalid Email");
    }catch(err){return err;}
  };
  let err=fetchUser();
  console.log(err);
  err?setError(err):navigate('/freelancer-web');
    
  };
  return (
    <Container
    sx={{ display: "flex", flexDirection: "column", alignItems: "center" }}
    >
      <Typography variant="h4" gutterBottom>Login</Typography>
      <Box>
      {error && <Alert severity='error'>{error.message}</Alert>}
      <TextField className="TextField" label="Email" fullWidth value={email} onChange={(e) => setEmail(e.target.value)} margin="normal" />
      <TextField className="TextField" label="Password" type="password" fullWidth value={password} onChange={(e) => setPassword(e.target.value)} margin="normal" />
      <Button sx={{ mt: "15px" }} variant="contained" color="primary" onClick={loginSample} fullWidth>Login</Button>
      <Typography sx={{my:"20px",width:"fullScreen",justifySelf:"center"}}>--or--</Typography>
      <Button onClick={()=>{    }}>Sign in with google</Button>
      </Box>
    </Container>
  );
};

export default Login;
