import React, { useEffect, useState } from "react";
import {
  Container,
  TextField,
  Button,
  Typography,
  FormControl,
  Select,
  MenuItem,
  Box,
  InputAdornment,
  Alert,
} from "@mui/material";
import "./Register.css"
import { AccountCircle, EmailRounded, LockPersonRounded} from "@mui/icons-material";
import Home from './Home';
import { Link, useNavigate } from "react-router-dom";
const Register = ({ setCurrentUser,userList,setUserList}) => {
  const navigate=useNavigate();
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [role, setRole] = useState("freelancer"); // Default role
  const [error, setError] = useState(null);

  const signinsample = async (e) => {
    let customId=userList.length!=0?parseInt(userList[userList.length-1].id)+1:1
    let user = {
      id:customId,
      name: name,
      email: email,
      pass: password,
      role: role,
      bio:"Student",
      avatar:"",
      skills: role === "freelancer" ? ["Python"] : undefined,
      jobs:[],
      badges:0,
      reviews:[],
      companyName: role === "client" ? "" : undefined,
      postedJobs: role === "client" ? [] : undefined,
    };
    let fetchUser=()=>{
    // document.querySelector(".container .formElement input[type=text]").value="";
    try{
      userList.forEach((user) => {if(user.email === email) throw new Error("Email already taken")});
      setUserList([...userList,user]);     
      console.log(userList);
      setCurrentUser(user);
    }catch(err){return err;}
  };
  let err=fetchUser();
  console.log(err);
  err?setError(err):navigate('/freelancer-web');
    
  };
  useEffect(()=>{
    console.log(userList);
  },[userList]);
  return (
    <Container
      sx={{ display: "flex", flexDirection: "column", alignItems: "center" }}
      
    >
      <Typography variant="h4" gutterBottom>
        Sign In
      </Typography>
      <Box component="form" noValidate sx={{ maxWidth: "500px" }} className="formElement">
      {error && <Alert  severity="error">{error.message}</Alert>}
        <TextField
          label="Name"
          fullWidth
          value={name}
          onChange={(e) => setName(e.target.value)}
          margin="normal"
          className="TextField"
          slotProps={{
            input: {
              startAdornment: (
                <InputAdornment position="start">
                  <AccountCircle />
                </InputAdornment>
              ),
            },
          }}
        />
        <TextField
          label="Email"
          fullWidth
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          margin="normal"
          className="TextField"
          slotProps={{
            input: {
              startAdornment: (
                <InputAdornment position="start">
                  <EmailRounded />
                </InputAdornment>
              ),
            },
          }}
        />
        <TextField
          label="Password"
          type="password"
          fullWidth
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          margin="normal"
          className="TextField"
          slotProps={{
            input: {
              startAdornment: (
                <InputAdornment position="start">
                  <LockPersonRounded />
                </InputAdornment>
              ),
            },
          }}
        />
        <TextField
          label="Role"
          select
          fullWidth
          value={role}
          onChange={(e) => setRole(e.target.value)}
          className="TextField"
          sx={{ mt: "12px" }}
        >
          <MenuItem value="freelancer">Freelancer</MenuItem>
          <MenuItem value="client">Client</MenuItem>
        </TextField>
        <Button
          sx={{ mt: "15px" }}
          variant="contained"
          color="primary"
          onClick={signinsample}
          fullWidth
        >
          Register
        </Button>
      </Box>
      <Typography variant="p" textAlign="center" my="20px">Already have an account <Link to="/freelancer-web/login">Login</Link> here</Typography>
      
    </Container>
  );
};

export default Register;
