import React from "react";
import {
  Drawer,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Toolbar,
  AppBar,
  Typography,
  IconButton,
  Button,
} from "@mui/material";

import HomeIcon from "@mui/icons-material/Home";
import PersonIcon from "@mui/icons-material/Person";
import WorkIcon from "@mui/icons-material/Work";
import SettingsIcon from "@mui/icons-material/Settings";
import MenuIcon from "@mui/icons-material/Menu";
import { Link, useNavigate } from "react-router-dom";
import "./SidebarSimple.css"; // Import the CSS file
const drawerWidth = 240;

const Sidebar = ({ handleDrawerToggle, mobileOpen ,currentUser,setCurrentUser}) => {
  let navigate=useNavigate();
  return (
    <>
      {/* AppBar for Top Navigation */}
      <AppBar
        sx={{
          width: { md: "100%" },
        }}
      >
        <Toolbar
          className="appBar"
          sx={{
            display: "flex",
            justifyContent: "space-between",
            flexWrap: "nowrap",
          }}
        >
          <div style={{ display: "flex", alignItems: "center" }}>
            <IconButton
              edge="start"
              color="inherit"
              aria-label="menu"
              onClick={handleDrawerToggle}
              sx={{ display: { md: "none" } }}
            >
              <MenuIcon />
            </IconButton>
            <Typography variant="h6" noWrap component="div" sx={{ mr: "15px" }}>
              Freelancer Platform
            </Typography>
            <List
              sx={{ display: { md: "flex", sm: "none", xs: "none" } }}
              className="menuInRow"
            >
              <ListItem>
                <Link to="/freelancer-web">Home</Link>
              </ListItem>
              <ListItem>
                <Link to="/freelancer-web/profile">Profile</Link>
              </ListItem>
              <ListItem>
                <Link to="/freelancer-web/jobs">Jobs</Link>
              </ListItem>
              <ListItem>
                <Link to="/freelancer-web/settings">Settings</Link>
              </ListItem>
            </List>
          </div>
          {!currentUser?
          <Link to="/freelancer-web/Register">
            <Button variant="contained">Sign In</Button>
          </Link>:<Button variant="contained" onClick={e=>{setCurrentUser(null);navigate('/freelancer-web');}}>Logout</Button>}
        </Toolbar>
      </AppBar>

      {/* Drawer (Sidebar) */}
      <Drawer
        variant="temporary"
        open={mobileOpen}
        onClose={handleDrawerToggle}
        ModalProps={{
          keepMounted: true, // Better open performance on mobile
        }}
        sx={{
          display: { sm: "block", md: "none" },
          "& .MuiDrawer-paper": { boxSizing: "border-box", width: drawerWidth },
        }}
      >
        <div className="sidebar">
          <div className="sidebar-content">
            <h2>Menu</h2>
            <ul>
              <li>
                  <HomeIcon/>
                <Link to="/freelancer-web/" onClick={handleDrawerToggle}>
                  Home
                </Link>
              </li>
              <li>
                <PersonIcon/>
                <Link to="/freelancer-web/profile" onClick={handleDrawerToggle}>
                  Profile
                </Link>
              </li>
              <li>
                <WorkIcon/>
                <Link to="/freelancer-web/jobs" onClick={handleDrawerToggle}>
                  Jobs
                </Link>
              </li>
              <li>
                <SettingsIcon/>
                <Link
                  to="/freelancer-web/settings"
                  onClick={handleDrawerToggle}
                >
                  Settings
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </Drawer>
    </>
  );
};

export default Sidebar;
