import React, { useState } from "react";
import { BrowserRouter as Router, Routes, Route, Link } from "react-router-dom";
import Sidebar from "./components/Sidebar";
import Home from "./pages/Home";
import Profile from "./pages/Profile";
import Jobs from "./pages/Jobs";
import Settings from "./pages/Settings";
import Login from "./pages/Login";
import Register from "./pages/Register";
// import Chat from './components/Chat';
// import ProtectedRoute from "./ProtectedRoute";
import { CssBaseline, Box, Toolbar, Typography } from "@mui/material";
import ProfilePage from "./pages/ProfilePage";

const App = () => {
  const [mobileOpen, setMobileOpen] = useState(false);
  const [currentUser,setCurrentUser] = useState(null);
  const [userList,setUserList] = useState([]);
  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };


  return (
    <Router>
      <Box sx={{ display: "flex" }}>
        <CssBaseline />
        <Sidebar
          handleDrawerToggle={handleDrawerToggle}
          mobileOpen={mobileOpen}
          currentUser={currentUser}
          setCurrentUser={setCurrentUser}
        />
        <Box component="main" sx={{ flexGrow: 1, p: 3 }}>
          <Toolbar />
          <Routes>
          <Route path="freelancer-web">
            <Route path="" element={<Home currentUser={currentUser}/>} />
            <Route path="profile" element={currentUser?<ProfilePage currentUser={currentUser} userList={userList} setCurrentUser={setCurrentUser} setUserList={setUserList}/>:<Typography>You not Loged In <br/>Please <Link to='/freelancer-web/register'>Sign In</Link> </Typography>} />
            <Route path="jobs" element={<Jobs />} />
            <Route path="settings" element={<Settings />} />
            <Route path="login" element={<Login setCurrentUser={setCurrentUser} userList={userList}  />} />
            <Route path="register" element={<Register setCurrentUser={setCurrentUser} userList={userList} setUserList={setUserList}/>} />
            {/* <Route path="/chat" element={<Chat />} /> */}
          </Route>
          </Routes>
        </Box>
      </Box>
    </Router>
  );
};

export default App;
