import React, { useState } from "react";
import {
  Container,
  Stack,
  Card,
  CardContent,
  Typography,
  Avatar,
  Button,
  TextField,
  Chip,
  Box,
  Autocomplete,
} from "@mui/material";
import "./Register.css";

const ProfilePage = ({
  currentUser,
  userList,
  setCurrentUser,
  setUserList,
}) => {
  const [name, setName] = useState(currentUser.name);
  const [bio, setBio] = useState(currentUser.bio);
  const [skillsState, setSkillsState] = useState(currentUser.skills);

  const skillsList = [
    "JavaScript",
    "Python",
    "Java",
    "C#",
    "C++",
    "Ruby",
    "PHP",
    "Swift",
    "Go",
    "Rust",
    "Kotlin",
    "TypeScript",
    "Scala",
    "Dart",
    "Objective-C",
    "Elixir",
    "Haskell",
    "Perl",
    "Lua",
    "R",
    "Shell",
    "Web Development",
    "Data Analysis",
    "Machine Learning",
    "Cloud Computing",
    "Mobile App Development",
    "DevOps",
    "UI/UX Design",
    "Project Management",
    "Cybersecurity",
    "Database Management",
    "Software Testing",
    "Network Administration",
    "SEO Optimization",
    "Graphic Design",
    "Content Creation",
    "Agile Methodologies",
    "Technical Writing",
    "Game Development",
    "Blockchain Development",
    "Artificial Intelligence",
  ];

  const user = {
    name: currentUser.name,
    email: currentUser.email,
    bio: currentUser.bio,
    avatar: "https://via.placeholder.com/150",
    skills: currentUser.skills,
    jobs: [
      { title: "Frontend Developer", company: "Tech Company A" },
      { title: "Backend Developer", company: "Tech Company B" },
    ],
    badges: 5,
    reviews: [
      { reviewer: "Alice", comment: "Great work!" },
      { reviewer: "Bob", comment: "Highly skilled and reliable." },
    ],
  };
  const [editMode, setEditMode] = useState(false);
  let handleEdit = (e) => {
    // setCurrentUser({...currentUser,name:name,bio:bio});
    let newUserList = userList.map((user) => {
      if (user.email === currentUser.email) {
        user.name = name;
        user.bio = bio;
        user.skills=skillsState;
      }
      return user;
    });
    setUserList(newUserList);
    console.log(userList);
    setEditMode(!editMode);
  };
  return (
    <Container maxWidth="md">
      {!editMode ? (
        <Stack spacing={3}>
          <Stack xs={12}>
            <Card>
              <CardContent style={{ display: "flex", alignItems: "center" }}>
                <Avatar
                  alt={user.name}
                  src={user.avatar}
                  sx={{ width: 100, height: 100, marginRight: 2 }}
                />
                <div>
                  <Typography variant="h4">{user.name}</Typography>
                  <Typography variant="subtitle1">{user.email}</Typography>
                  <Typography variant="body2" color="textSecondary">
                    {user.bio}
                  </Typography>

                  <Button
                    variant="contained"
                    color="primary"
                    sx={{ marginTop: 2 }}
                    onClick={() => {
                      setEditMode(!editMode);
                    }}
                  >
                    Edit Profile
                  </Button>
                </div>
              </CardContent>
            </Card>
          </Stack>
          <Stack xs={12}>
            <Card>
              <CardContent>
                <Typography variant="h5">Skills</Typography>
                <div>
                  {user.skills.map((skill) => (
                    <Chip key={skill} label={skill} sx={{ margin: "4px" }} />
                  ))}
                </div>
              </CardContent>
            </Card>
          </Stack>

          <Stack xs={12}>
            <Card>
              <CardContent>
                <Typography variant="h5">Jobs Done</Typography>
                {user.jobs.map((job, index) => (
                  <Typography key={index} variant="body1">
                    {job.title} at {job.company}
                  </Typography>
                ))}
              </CardContent>
            </Card>
          </Stack>

          <Stack xs={12}>
            <Card>
              <CardContent>
                <Typography variant="h5">Badges Earned</Typography>
                <Typography variant="h6">{user.badges}</Typography>
              </CardContent>
            </Card>
          </Stack>

          <Stack xs={12}>
            <Card>
              <CardContent>
                <Typography variant="h5">Reviews</Typography>
                {user.reviews.map((review, index) => (
                  <Typography key={index} variant="body1">
                    <strong>{review.reviewer}:</strong> {review.comment}
                  </Typography>
                ))}
              </CardContent>
            </Card>
          </Stack>
        </Stack>
      ) : (
        <Box>
          <TextField
            className="TextField"
            label="Name"
            sx={{ my: "10px" }}
            defaultValue={user.name}
            fullWidth
            onChange={(e) => {
              setName(e.target.value);
            }}
          />
          <TextField
            className="TextField"
            label="Bio"
            sx={{ my: "10px" }}
            defaultValue={user.bio}
            fullWidth
            onChange={(e) => {
              setBio(e.target.value);
            }}
          />
          <Autocomplete
            multiple
            id="tags-outlined"
            options={skillsList}
            filterSelectedOptions
            defaultValue={user.skills}
            renderInput={(params) => (
              <TextField
                {...params}
                label="Skills"
                placeholder="Choose a Skill"
                className="TextField"
              />
            )}
            onChange={(e,value) => setSkillsState(value)}
          />
          <Button
            variant="contained"
            color="primary"
            sx={{ ml: "30px", mt: "20px" }}
            onClick={handleEdit}
          >
            Save Changes
          </Button>
        </Box>
      )}
    </Container>
  );
};

export default ProfilePage;
