import React, { useState } from 'react';
import './Settings.css';

const Settings = () => {
  const [profile, setProfile] = useState({
    name: 'John Doe',
    email: 'john.doe@example.com',
  });

  const [notifications, setNotifications] = useState({
    emailNotifications: true,
    smsNotifications: false,
  });

  const [privacy, setPrivacy] = useState({
    showProfileToPublic: true,
    showActivityStatus: false,
  });

  const [password, setPassword] = useState({
    currentPassword: '',
    newPassword: '',
    confirmPassword: '',
  });

  // Handle changes for input fields
  const handleProfileChange = (e) => {
    setProfile({ ...profile, [e.target.name]: e.target.value });
  };

  const handleNotificationsChange = (e) => {
    setNotifications({ ...notifications, [e.target.name]: e.target.checked });
  };

  const handlePrivacyChange = (e) => {
    setPrivacy({ ...privacy, [e.target.name]: e.target.checked });
  };

  const handlePasswordChange = (e) => {
    setPassword({ ...password, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // Logic for handling form submission or API call to save settings
    console.log('Settings saved:', { profile, notifications, privacy, password });
  };

  return (
    <div className="settings-page">
      <h2>Settings</h2>
      <form onSubmit={handleSubmit}>
        {/* Profile Settings Section */}
        <div className="settings-section">
          <h3>Profile Settings</h3>
          <label>
            Name:
            <input type="text" name="name" value={profile.name} onChange={handleProfileChange} />
          </label>
          <label>
            Email:
            <input type="email" name="email" value={profile.email} onChange={handleProfileChange} />
          </label>
        </div>

        {/* Notification Settings Section */}
        <div className="settings-section">
          <h3>Notification Settings</h3>
          <label>
            <input
              type="checkbox"
              name="emailNotifications"
              checked={notifications.emailNotifications}
              onChange={handleNotificationsChange}
            />
            Email Notifications
          </label>
          <label>
            <input
              type="checkbox"
              name="smsNotifications"
              checked={notifications.smsNotifications}
              onChange={handleNotificationsChange}
            />
            SMS Notifications
          </label>
        </div>

        {/* Privacy Settings Section */}
        <div className="settings-section">
          <h3>Privacy Settings</h3>
          <label>
            <input
              type="checkbox"
              name="showProfileToPublic"
              checked={privacy.showProfileToPublic}
              onChange={handlePrivacyChange}
            />
            Show Profile to Public
          </label>
          <label>
            <input
              type="checkbox"
              name="showActivityStatus"
              checked={privacy.showActivityStatus}
              onChange={handlePrivacyChange}
            />
            Show Activity Status
          </label>
        </div>

        {/* Password Change Section */}
        <div className="settings-section">
          <h3>Change Password</h3>
          <label>
            Current Password:
            <input
              type="password"
              name="currentPassword"
              value={password.currentPassword}
              onChange={handlePasswordChange}
            />
          </label>
          <label>
            New Password:
            <input
              type="password"
              name="newPassword"
              value={password.newPassword}
              onChange={handlePasswordChange}
            />
          </label>
          <label>
            Confirm New Password:
            <input
              type="password"
              name="confirmPassword"
              value={password.confirmPassword}
              onChange={handlePasswordChange}
            />
          </label>
        </div>

        {/* Save Button */}
        <button type="submit" className="save-button">Save Changes</button>
      </form>
    </div>
  );
};

export default Settings;
